<template>
    <div class="hbHistory_container">
        <head-top head-title="历史红包" goBack="true"></head-top>
        <section class="hbHistory-list"  v-if="!showLoading" id="better-scrolls">
            <ul class="hbHistory-ul">
                <li v-for="(item) in expiredList" :key="item.id">
                    <section class="hbHistory-body">
                        <div class="hbHistory-jia">
                            <span>￥</span>
                            <span>{{String(item.amount).split('.')[0]}}</span>
                            <span>.</span>
                            <span>{{String(item.amount).split('.')[1] || 0}}</span>
                            <p>{{item.description_map.sum_condition}}</p>
                        </div>
                        <div class="hbHistory-right">
                            <div class="xs-left">
                                    <p>{{item.name}}</p>
                                    <p>{{item.description_map.validity_periods}}</p>
                                    <p>{{item.description_map.phone}}</p>
                                </div>
                                <div class="xs-right">
                                    <svg class="expired">
                                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#expired"></use>
                                    </svg>
                                </div>
                        </div>
                    </section>
                    <footer class="zhu_yi" v-if="item.limit_map">
                        <p v-if="item.limit_map">{{item.limit_map.restaurant_flavor_ids}}</p>
                    </footer>
                </li>
            </ul>
        </section>
        <loading v-show="showLoading"></loading>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import headTop from '../../../components/header/header'
import {getExpired} from '../../../service/getData'
import loading from '../../../components/common/loading'
import BScroll from 'better-scroll'
export default {
    components: {
        headTop,
        loading
    },
    data(){
        return{
            showLoading:true,
            expiredList:null,//历史红包
        }
    },computed: {
        ...mapState([
            'userInfo'
        ])
    },methods: {
        ...mapMutations([
            'CLEAR_CART'
        ]),
        async initdata()
        {
            this.showLoading=true;
            if(this.userInfo && this.userInfo.user_id)
            {
                let res=await getExpired(this.userInfo.user_id);
                this.expiredList=res;
                 this.showLoading=false;
                //this.$nextTick等所有事件完成之后在执行
                // this.$nextTick(()=>{
                //     new BScroll('#better-scrolls',{
                //         deceleration:0.001,
                //         bounce:true,
                //         swipeTime:100,
                //         click:true
                //     })
                // })

            }
        }
    },mounted () {
        this.initdata();
    },watch: {
        userInfo:function(value)
        {
            this.initdata();
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.hbHistory_container{
    overflow-y: auto;
    position:fixed;
    top: 0;
    left:0;
    right:0;
    bottom: 0;
    background: #f5f5f5;
    z-index: 250;
              p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
        }
    padding-top: 2.3rem;
}
.hbHistory-list{
    .hbHistory-ul{
        padding: 1rem .5rem;
        li{
            background: #fff url(../../../images/expired.png) repeat-x;
            margin:0 0.2rem;
            border-radius: 0.2rem;
            margin-bottom: .6rem;
            .zhu_yi{
                border-radius: 0.2rem;
                p{
                    @include sc(0.5rem,#ccc);
                }
                -webkit-tap-highlight-color: transparent;
                -webkit-font-smoothing: antialiased;
                padding:0.4rem;
                background: #f1f1f1;
                text-align: center;
            }
            .hbHistory-body{
                padding:0.4rem;
                display: flex;
                .hbHistory-jia{
                    padding:0.4rem 0;
                    border-right: 0.025rem solid #ddd;
                    flex:1;
                    font-size: 0;
                   span{
                       color:#666;
                       font-size: 0.8rem;
                   }
                    span:nth-of-type(1)
                    {
                        font-weight: bold;
                    }
                    span:nth-of-type(2)
                    {
                        font-size: 1.5rem;
                        font-weight: 500;
                        margin-left:0.1rem;
                    }
                    p{
                        @include sc(0.2rem,#d1d1d1);
                    }
                }
                .hbHistory-right{
                    flex:2;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .xs-left{
                        justify-content: center;
                        padding-top:0.8rem;
                        padding-left:0.5rem;
                        p{
                            @include sc(.5rem,#666);
                            padding-bottom: 0.1rem;
                            white-space: nowrap;
                        }
                        p:nth-of-type(1)
                        {
                            @include sc(0.6rem,#333);
                            font-weight: bold;
                        }
                    }
                    .xs-right{
                        display: flex;
                        align-items: center;
                        svg{
                            @include wh(3rem,3rem);
                        }
                    }
                }
            }
        }
    }
}
</style>



